/* eslint-disable no-unused-vars */
import React, { useContext } from "react";
import Style from "../css/SideMenu.module.css";
import {
  Link,
  useRouteMatch,
  withRouter,
  Redirect,
  useLocation,
} from "react-router-dom";
import { stateContext } from "../../../StateContext";
import * as IconAI from "react-icons/ai";
import * as IconBS from "react-icons/bs";
import * as IconRi from "react-icons/ri";
import Cookies from "js-cookie";
/*Redux*/
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "../../../state/index";
/***** */

const NavLink = ({ to, ...rest }) => {
  const path = useLocation();
  const IsActive = to === path.pathname;
  return (
    <Link
      {...rest}
      to={to}
      className={IsActive ? `${Style.Link} ${Style.active}` : `${Style.Link}`}
    />
  );
};

function SideMenu({ history }) {
  let [Logedin, setLogin] = useContext(stateContext);
  const state = useSelector((state) => state.User);

  const dispatch = useDispatch();

  const { login, logout } = bindActionCreators(actionCreators, dispatch);

  console.log(state);
  const { url } = useRouteMatch();
  return (
    <div className={Style.sideMenuWrapper}>
      <div className={Style.Header}>
        <div className={Style.Img}>
          <img
            src="https://randomuser.me/api/portraits/women/44.jpg"
            alt="Prifile_Picture"
          />
        </div>{" "}
        <div className={Style.Name}>
          {" "}
          <h4> Jon Do. </h4>{" "}
        </div>{" "}
      </div>{" "}
      <div className={Style.Actions}>
        <ul>
          <li>
            <NavLink to={`${url}`}>
              <IconAI.AiOutlineEye />
              <p> View Info. </p>{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink to={`${url}/Edite`}>
              <IconBS.BsPencil />
              <p> Edite Info. </p>{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink to="/Config_account">
              <IconRi.RiLockPasswordLine />
              <p> Change Password. </p>{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <input
              className={Style.Logoutinput}
              type="button"
              value="Log Out"
              onClick={() => {
                logout(false);
                Cookies.remove("user");
                // history.push("/");
                <Redirect to="/" />;
              }}
            />{" "}
          </li>{" "}
        </ul>{" "}
      </div>{" "}
    </div>
  );
}

export default withRouter(SideMenu);
